@-webkit-keyframes appear {
  0%{ opacity: 0; left: -200px;}
  100%{ opacity: 1;  left:0;}
}
@keyframes appear {
  0%{ opacity: 0; left: -200px;}
  100%{ opacity: 1;  left:0;}
}
@-webkit-keyframes appear1 {
  0%{ opacity: 0;}
  100%{ opacity: 1;}
}
@keyframes appear1 {
  0%{ opacity: 0;}
  100%{ opacity: 1;}
}
@-webkit-keyframes appear2 {
  0%{ opacity: 0; top: 100%;}
  100%{ opacity: 1;  top:0%;}
}
@keyframes appear2 {
  0%{ opacity: 0; top: 100%;}
  100%{ opacity: 1;  top:0%;}
}
@-webkit-keyframes appear3 {
  0%{ opacity: 0; top: 300px;}
  100%{ opacity: 1;  top:50px;}
}
@keyframes appear3 {
  0%{ opacity: 0; top: 300px;}
  100%{ opacity: 1;  top:50px;}
}
@-webkit-keyframes disappear {
  0%{ opacity: 1; }
  100%{ opacity: 0;  top:400px;}
}
@keyframes disappear {
  0%{ opacity: 1; }
  100%{ opacity: 0;  top:400px;}
}
@-webkit-keyframes slideInLeft {
  0%{left:-15vw;}
  100%{left:0;}
}
@keyframes slideInLeft {
  0%{left:-15vw;}
  100%{left:0;}
}
@-webkit-keyframes slideInRight {
  0%{right:-15vw;}
  100%{right:0;}
}
@keyframes slideInRight {
  0%{right:-15vw;}
  100%{right:0;}
}
@-webkit-keyframes contract {
  0%{width: 100vw; margin-left: 0vw;}
  100%{width: 70vw; margin-left: 15vw;}
}
@keyframes contract {
  0%{width: 100vw; margin-left: 0vw;}
  100%{width: 70vw; margin-left: 15vw;}
}
